*{
  margin: 0;
  font-family: Arial;
  border:border-box;
  }

  .navbar{
      height:85px;
      background-color: #0f1111;
      color:white;
      display:flex;
      align-items: center;
      justify-content: space-evenly;
      padding-bottom: 30px;
}
.nav-logo{
  height:49px;
  width:100px;
  padding-left: 5px;
}

.logo{
  background-image: url("amazon_logo.jpg");
  background-size:cover;
  height:100%;
  width:120%;
}

.borders{
  border : 1.5px solid transparent;
}

.borders:hover{
  border:1.5px solid white;
}

/**box2 **/
.add-first{
  color:#cccccc;
  font-size:0.85rem;
  margin-left: 10px;
}

.add-second{
  font-size:1rem;
  margin-left:3px;
}

.add-icon{
  display:flex;
  align-items:center;
}

/** box3 **/

.nav-search{
  display:flex;
  justify-content: space-evenly;
  background-color:orange;
  width:620px;
  height:40px;
  border-radius:4px;
}

.search-select{
 background-color:#F3F3F3; 
 width:50px;
 text-align:center;
  border-top-left-radius:4px;
  border-bottom-left-radius:4px;
  border:none;
}

.search-input{
  width:100%;
  font-size:1rem;
  border:none;
}

.search-icon{
  width:45px;
  display:flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  background-color:#febd68;
  border-top-right-radius:4px;
  border-bottom-right-radius:4px;
  color:#0f1111;
}

.nav-search:hover{
  border:2px solid orange;
}

/** box4 **/

.span{
  font-size:0.7rem ;
}

.nav-second{
  font-size: 0.85rem; 
  font-weight: 700;

}

/** box6 **/

.nav-cart i{
  font-size: 30px;
}

.nav-cart{
 font-size:0.85rem ; 
 font-weight: 700;
}
/**panel**/

.panel{
  height : 40px;
  background-color: #222f3d;
  display :flex;
  color:white;
  align-items: center;
  justify-content: space-evenly;
  margin-top: 85px;
}
.panel-ops p{
  display:inline;
  margin-left: 10px;
  font-size: medium;
}
.panel-ops{
  width:70%;
  font-size: 0.85rem;
}

.panel-deals{
 font-size:0.9rem; 
 font-weight: 700;
}

